import React from 'react';
//import logo from './logo.svg';
import './App.css';
import './data.js';
import today from './data.js';
import ReactDOM from 'react-dom';

function App() {
  this.setstate = {filters : {
    dateFrom: today, // Proviene del archivo data.js
    dateTo: new Date(today.valueOf() + 86400000),
    country: '',
    price: 0,
    rooms: 0
  }}
  return (
    <div>
      <Hero filters={this.setstate.filters } />
      <Filters filters={this.setstate.filters } />
    </div>
  )}
function Hero(props){
  const formato = {weekday: "long",
  year: "numeric", 
  month: "long", 
  day: "numeric", 
}
  return (
    <section className="hero is-primary">
    <div className="hero-body">
      <div className="container">
        <h1 className="title">Hoteles</h1>
        <h2 className="subtitle">
          desde el <strong>{today.toLocaleString("es-ES", formato)}</strong> hasta el <strong>dddd, DD de mmmm de AAAA</strong>
        </h2>
      </div>
    </div>
  </section>
  )
}
class DateFilter extends React.Component {
  constructor(props){super(props)
  this.handledatechange =this.handledatechange.bind(this)}
  handledatechange(event){
    this.props.ondatechange(event)}
  render(){
    return(
  <div className="field">
  <input className="input" type="date" onchange={this.handledatechange} value={this.date} name={this.props.name} />
  <div className="control has-icons-left">
    <span className="icon is-small is-left">
      <i className="fas"></i>
    </span>
  </div>
</div>)}
}
function OptionsFilter(){
  const opciones=[
    { value: undefined, name: 'Cualquier tamaño' },
    { value: 10, name: 'Hotel pequeño' },
    { value: 20, name: 'Hotel mediano' },
    { value: 30, name: 'Hotel grande' }
  ]
  return(
  <div className="field">
  <div className="control has-icons-left">
    <div className="select" style={ {width: '100%'} }>
      <select style={ {width: '100%'} }>
        ...
      </select>
    </div>
    <div className="icon is-small is-left">
  <i className="fas">{opciones.map}</i>
    </div>
  </div>
</div>)
}
class Filters extends React.Component{
  constructor(props){super(props)
    this.handledatechange =this.handledatechange.bind(this)}
  handleOptionChange(event) {
    let payload = this.props.filters
    payload[event.target.name] = event.target.value
  
    this.props.onFilterChange(payload)
  }
  render() {
  return(
<nav className="navbar is-info" style={ {justifyContent: 'center'} }>
  <div className="navbar-item">
    <DateFilter
      date={ this.props.filters.dateFrom}
      icon="sign-in-alt" />
  </div>
  <div className="navbar-item">
    <DateFilter
      date={ this.props.filters.dateTo }
      icon="sign-out-alt" />
  </div>
  <div className="navbar-item">
  <OptionsFilter
      options={ [ {value: undefined, name: 'Todos los países'}, {value: 'Argentina', name: 'Argentina'}, {value: 'Brasil', name: 'Brasil'}, {value: 'Chile', name: 'Chile'}, {value: 'Uruguay', name: 'Uruguay'} ] }
      selected={ this.props.filters.country }
      icon="globe" />
  </div>
  <div className="navbar-item">
    <OptionsFilter
      options={ [ {value: undefined, name: 'Cualquier precio'}, {value: 1, name: '$'}, {value: 2, name: '$$'}, {value: 3, name: '$$$'}, {value: 4, name: '$$$$'} ] }
      selected={ this.props.filters.price }
      icon="dollar-sign" />
  </div>
  <div className="navbar-item">
    <OptionsFilter
      options={ [ {value: undefined, name: 'Cualquier tamaño'}, {value: 10, name: 'Hotel pequeño'}, {value: 20, name: 'Hotel mediano'}, {value: 30, name: 'Hotel grande'} ] }
      selected={ this.props.filters.rooms }
      icon="bed" />
  </div>
</nav>
  )
}}

ReactDOM.render(
  <App />,
document.getElementById('root')
);

export default App;
